.product {
  display: flex;
  flex-direction: column;

  padding-top: 121px;
  padding-bottom: 98px;

  @media screen and (min-width: 768px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.product-label {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: #ffa5ba;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 15px;
  margin-left: auto;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.product-header {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #d41443;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 144px;
  margin-left: auto;

  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: 1.4;
    margin-bottom: 150px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
    margin-bottom: 259px;
  }
}

.product-list {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 768px) {
    flex-direction: row;
  }

  @media screen and (min-width: 1280px) {
    flex-direction: row;
  }
}

.product-item__ice {
  width: 280px;
  background-color: #ffb8ca;
  border-radius: 24px;
  margin-bottom: 130px;
 
  @media screen and (min-width: 768px) {
    margin-right: 20px;
    width: 220px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 24px;
    width: 335px;
  }
}

.product-item__coffe {
  width: 280px;
  background-color: #f0d1a5;
  border-radius: 24px;
  margin-bottom: 130px;
 
  @media screen and (min-width: 768px) {
    margin-right: 20px;
    width: 220px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 24px;
    width: 335px
  }
}

.product-item__shake {
  width: 280px;
  background-color: #c2e297;
  border-radius: 24px;
  // margin-bottom: 130px;
  
  @media screen and (min-width: 768px) {
    width: 220px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    width: 335px;
  }
}

.product-list__header-ice {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;
  padding-top: 161px;
  padding-left: 53px;
  padding-right: 53px;
  position: relative;
  margin-bottom: 66px;

   @media screen and (min-width: 768px) {
     font-size: 18px;
     line-height: 1.16;
   }
  
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.15;
  }
}

.product-list__header-ice::before {
  content: '';
  background-image: url('../images/ice-mob.png');
  width: 249px;
  height: 252px;
  position: absolute;
  left: 20px;
  top: -115px;

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url('../images/ice-mob@2x.png');
  }
    

  @media screen and (min-width: 768px) {
    background-image: url('../images/ice-tab.png');
    width: 220px;
    height: 224px;
    left: 5px;
    top: -103px;
    
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/ice-tab@2x.png');
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url('../images/ice-dt.png');
    width: 335px;
    height: 335px;
    top: -195px;

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url('../images/ice-dt@2x.png');
    }
  }

}

.product-list__header-ice::after {
  content: '';
  background-image: url('../images/dots.png');
  position: absolute;
  width: 26px;
  height: 6px;
  right: 132px;
  bottom: -30px;

  @media screen and (min-width: 768px) {
    right: 100px;
  }

  @media screen and (min-width: 768px) {
    right: 160px;
  }
}

