.product-list__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.5;

  letter-spacing: 0.04em;
  color: #ffffff;

  display: flex;
  text-align: center;
  flex-direction: column;
  position: inherit;
  
  width: 200px;
 
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    width: 198px;
  }

  @media screen and (min-width: 1280px) {
    width: 241px;
  }
}

.product-list__arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 auto;
  position: inherit;
  margin-bottom: 41px;
}